<template>
  <div class="section">
    <nav>
      <a href="#section-1" :class="{ active: activeSection === 1 }"
        >Section 1</a
      >
      <a href="#section-2" :class="{ active: activeSection === 2 }"
        >Section 2</a
      >
      <a href="#section-3" :class="{ active: activeSection === 3 }"
        >Section 3</a
      >
    </nav>
    <section id="section-1">Section 1</section>
    <section id="section-2">Section 2</section>
    <section id="section-3">Section 3</section>
  </div>
</template>

<script>
export default {
  name: "test",
  data() {
    return {
      activeSection: 1,
    };
  },
  mounted() {
    const section = document.querySelector(".section");
    let isScrolling = false;

    const sectionCount = 3; // 총 섹션 수
    const sectionHeight = window.innerHeight; // 섹션 높이
    const maxScrollY = sectionCount * sectionHeight - window.innerHeight; // 최대 스크롤 위치

    section.addEventListener(
      "wheel",
      (e) => {
        console.log(isScrolling);
        e.preventDefault();
        if (isScrolling) {
          return;
        } else {
          isScrolling = true; // 스크롤 시작시 isScrolling 값을 true로 변경
          const deltaY = e.deltaY;

          if (deltaY > 0) {
            const currentY = window.pageYOffset;
            const nextY = Math.min(currentY + sectionHeight, maxScrollY);
            window.scrollBy({
              top: nextY - currentY,
              behavior: "smooth",
            });
          } else if (deltaY < 0) {
            const currentY = window.pageYOffset;
            const prevY = Math.max(currentY - sectionHeight, 0);
            window.scrollBy({
              top: prevY - currentY,
              behavior: "smooth",
            });
          }
          setTimeout(() => {
            isScrolling = false; // 스크롤 종료시 isScrolling 값을 false로 변경
          }, 1000); // 적절한 시간(예: 1초) 후에 변경되도록 설정
        }
      },
      { passive: false }
    );
  },
  methods: {
    handleScroll() {
      const windowHeight = window.innerHeight;
      const section1 = document.getElementById("section-1");
      const section2 = document.getElementById("section-2");
      const section3 = document.getElementById("section-3");

      if (section1.getBoundingClientRect().top < windowHeight / 2) {
        this.activeSection = 1;
      } else if (section2.getBoundingClientRect().top < windowHeight / 2) {
        this.activeSection = 2;
      } else if (section3.getBoundingClientRect().top < windowHeight / 2) {
        this.activeSection = 3;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.active {
  color: red;
}
#section-1 {
  height: 100vh;
}
#section-2 {
  height: 100vh;
}
#section-3 {
  height: 100vh;
}
</style>
